import React from 'react';
import './InputForm.css';

const InputForm = ({onInputChange, onSubmit}) => {
   return(
      <div>
         <p className='f3'>This is a face recognition app. Insert image link into the box.</p>
         <div className='center pl4 pr4'>
            <div className='form center m3 pa4 shadow-5'>
               <input type='text' className='w-70 pa2' onChange={onInputChange}/>
               <button className='w-30 pa2 bg-light-purple white dib grow f5' onClick={onSubmit}>DETECT</button>            
            </div>
         </div>
      </div>
   )
}
export default InputForm;