import React from 'react';
import Tilt from 'react-tilt';
import face from './icons8-face-id-100.png';
import './Logo.css';

const Logo = () => {
   return(
      <div className='ma3'>
         <Tilt className="Tilt br2 shadow-2" options={{ max : 35}} style={{ height: 100, width: 100 }} >
            <div className="Tilt-inner">
               <img src={face} alt='face-logo' style={{paddingTop: '13px'}} />
            </div>
         </Tilt>
      </div>
   )
}

export default Logo;