import React from 'react';
import Clarifai from 'clarifai';
import './App.css';
import Navigation from './Components/Navigation/Navigation';
import Logo from './Components/Logo/Logo';
import Rank from './Components/Rank/Rank';
import InputForm from './Components/InputForm/InputForm';
import ImageRecognition from './Components/ImageRecognition/ImageRecognition';
import Particles from 'react-particles-js';
import Signin from './Components/Signin/Signin';
import Signup from './Components/Signup/Signup';

let app = new Clarifai.App({
  apiKey: '78674116bbf34c4c9c5661ea5b36f279'
});

const particlesValues = {
  particles: {
    number: {
      value: 170
    },
    size: {
      value: 3
    }
  },
  interactivity: {
    events: {
      onhover: {
        enable: false,
        mode: "repulse"
      }
    }
  }
}

class App extends React.Component {

  constructor(){
    super();
    this.state = {
      input : '',
      imageUrl : '',
      box: '',
      route: 'Signin'
    }
  }

  onRouteChange = (route) => {
    this.setState({route: route})
  }

  calculateFaceLocation = (data) => {
    const bounding_box = data.outputs[0].data.regions[0].region_info.bounding_box;
    const image = document.getElementById('input_img');
    const width = Number(image.width);
    const height = Number(image.height);
    return {
      leftCol: bounding_box.left_col*width+30, 
      rightCol: width-(bounding_box.right_col*width)+30, 
      topRow: bounding_box.top_row*height, 
      bottomRow: height-(bounding_box.bottom_row*height) 
    }
  }
  

  displayFaceBox = (box) => {
    this.setState({box: box})
  }
    
  onInputChange = (event) => {
    this.setState({input: event.target.value});
  }

  onSubmit = () => {
    this.setState({imageUrl: this.state.input})
    app.models
    .initModel({id: Clarifai.FACE_DETECT_MODEL})
    .then(faceModel => faceModel.predict(this.state.input))
    .then(response => this.displayFaceBox(this.calculateFaceLocation(response)))
  }

  render(){
    return (
      <div className="App">
        <Particles 
          className='particles'
          params={particlesValues} 
        />

        {this.state.route === 'home' 
          ?
            <div>
              <div className='between pa4'>
                <Logo />
                <Navigation onRouteChange={this.onRouteChange}/>
              </div>
              <Rank />
              <InputForm onInputChange={this.onInputChange} onSubmit={this.onSubmit}/>
              <ImageRecognition imageUrl={this.state.imageUrl} box={this.state.box}/>
            </div>          
          : (
              this.state.route === 'Signin'
                ? <Signin onRouteChange={this.onRouteChange}/>
                : <Signup onRouteChange={this.onRouteChange}/>
            ) 
        }
      </div>
    );
  }
}

export default App;
